html, body {
  width: 100%;
  height: 100%;
  font-family: var(--font-inter);
  color: var(--text-light);
  scroll-behavior: smooth;
  background-color: #1e1e1e;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.25;
}

.no-scroll {
  padding-right: 15px;
  overflow: hidden;
}

.page__wrapper {
  min-height: 100%;
  flex-direction: column;
  display: flex;
}

.main {
  flex-grow: 1;
}

button {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  cursor: pointer;
}

.section {
  background-color: var(--background-primary);
  border-radius: max(1.25rem, min(.75rem + 2.5vw, 3rem));
  margin-top: 1px;
  padding-top: max(1.875rem, min(.268rem + 8.04vw, 7.5rem));
  padding-bottom: max(1.875rem, min(.268rem + 8.04vw, 7.5rem));
}

.container {
  max-width: 1440px;
