  margin: 0 auto;
  padding-left: max(.938rem, min(10.27vw - 1.116rem, 8.125rem));
  padding-right: max(.938rem, min(10.27vw - 1.116rem, 8.125rem));
}

:root {
  --font-inter: "Inter", sans-serif;
  --font-roboto: "Roboto", sans-serif;
  --text-light: #fff;
  --text-dark: #0a0c0f;
  --text-24: rgba(255, 255, 255, .24);
  --text-36: rgba(255, 255, 255, .36);
  --text-56: rgba(255, 255, 255, .56);
  --text-08: rgba(255, 255, 255, .08);
  --accent-primary: #43a1f7;
  --rose-1: #ff68c3;
  --rose-2: #ea68ff;
  --rose-3: #cf68ff;
  --light-color: #fff;
  --dark-color: #000;
  --background-primary: #0a0c0f;
  --background-blue: rgba(67, 161, 247, .16);
  --background-04: rgba(255, 255, 255, .04);
  --background-08: rgba(255, 255, 255, .08);
  --background-12: rgba(255, 255, 255, .12);
  --background-50: rgba(255, 255, 255, .5);
  --background-80: rgba(255, 255, 255, .8);
  --background-rose: rgba(255, 104, 195, .16);
  --linear-grad-main: linear-gradient( to left, #577cff, #9b4cff, #fb4a4a, #9b4cff, #43d7f7, #00f69e );
  --linear-grad-blue: linear-gradient(268deg, #577cff 0%, #4cbfff 100%);
  --linear-grad-rose: linear-gradient(268deg, #fb4a4a 0%, #9b4cff 100%);
  --linear-grad-yellow: linear-gradient(44deg, #ffc657 24.69%, #ffd977 75.86%);
  --linear-grad-green: linear-gradient(180deg, #43d7f7 0%, #00f69e 100%);
  --linear-grad-rose-blue: linear-gradient( 90deg, #ba62ff 18.51%, #57b1f2 80.43% );
  --radial-grad-seven: radial-gradient( 57219.2% 137.55% at 92.97% 0%, rgba(67, 161, 247, .8) 0%, rgba(134, 67, 220, .8) 14.06%, rgba(247, 67, 175, .8) 28.65%, rgba(247, 128, 112, .8) 47.85%, rgba(190, 64, 255, .8) 57.29%, rgba(32, 188, 255, .8) 71.88%, rgba(0, 246, 158, .8) 100% );
  --radial-grad-rose: radial-gradient( 38.86% 38.86% at 50% 50%, rgba(255, 108, 223, .24) 0%, rgba(255, 108, 188, 0) 100% );
  --radial-grad-violet: radial-gradient( 38.86% 38.86% at 50% 50%, #b153fb 0%, #6944ff 100% );
  --radial-grad-blue: radial-gradient( 38.86% 38.86% at 50% 50%, #6743f7 0%, #02e4d6 100% );
  --radial-grad-green: radial-gradient( circle at center, #43a1f7 0%, #00f69e 100% );
  --radial-grad-orange: radial-gradient( 38.86% 38.86% at 50% 50%, rgba(255, 168, 119, .16) 0%, rgba(255, 217, 119, 0) 100% );
  --mobile: 320px;
  --tablet-small: 480px;
  --tablet-big: 768px;
  --desktop-small: 1024px;
  --desktop-big: 1200px;
}

.visually--hidden {
  visibility: hidden;
  height: 0;
}

.header__message {
  width: 100%;
  border-image-slice: 1;
  border-image-source: var(--linear-grad-main);
  border-bottom: 1px solid;
  justify-content: center;
  align-items: flex-start;
  padding: 8px 0;
  display: flex;
  position: relative;
}

.header__message.display-none {
  display: none;
}

.header__message-text--wrapper {
  max-width: 740px;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0 30px;
  display: flex;
}

.message-text {
  text-align: center;
  letter-spacing: -.169px;
  color: var(--text-56);
  text-wrap: balance;
  font-size: max(.75rem, min(.716rem + .17vw, .813rem));
  font-weight: 600;
  line-height: 1.5;
}

.message__block {
  margin-left: 3px;
  display: flex;
}

.message__read-btn {
  letter-spacing: -.169px;
  color: var(--text-light);
  align-items: center;
  margin-left: max(.5rem, min(.214rem + 1.43vw, 1.5rem));
  font-size: max(.75rem, min(.716rem + .17vw, .813rem));
  font-weight: 700;
  line-height: 1.38;
  display: flex;
}

@media screen and (min-width: 910px) {
  .message__read-btn {
    color: var(--text-36);
  }
}

.message__read-arrow {
  width: 14px;
  height: 14px;
  margin-left: 6px;
}

@media screen and (min-width: 910px) {
  .message__read-arrow {
    display: none;
  }
}

.message__btn--close {
  width: 16px;
  height: 16px;
  fill: var(--background-50);
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.burger-menu {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 50px;
  cursor: pointer;
  margin-left: auto;
  display: none;
}

@media screen and (max-width: 909.8px) {
  .burger-menu {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5px;
    display: flex;
  }
}

.burger-menu.is-open .bar:nth-child(2) {
  opacity: 0;
}

.burger-menu.is-open .bar:nth-child(1) {
  transform: translateY(8px)rotate(45deg);
}

.burger-menu.is-open .bar:nth-child(3) {
  transform: translateY(-8px)rotate(-45deg);
}

.bar {
  width: 24px;
  height: 3px;
  background-color: var(--light-color);
  transition: all .3s ease-in-out;
  display: block;
}

.mobile__menu {
  z-index: 10;
  width: max(19.375rem, min(16.692rem + 13.41vw, 25rem));
  background: var(--linear-grad-blue);
  border-bottom-left-radius: 20px;
  flex-direction: column;
  align-items: flex-end;
  padding: 30px 50px;
  transition: top .3s ease-in-out;
  display: flex;
  position: absolute;
  top: -150%;
  right: 0;
}

.mobile__nav-list {
  flex-direction: column;
  align-items: flex-end;
  gap: 20px;
  display: flex;
}

.mobile__nav-item {
  color: var(--light-color);
  align-items: center;
  font-size: 24px;
  font-weight: 600;
  display: flex;
}

.mobile__nav-link {
  padding: 10px;
}

.mobile__user-name {
  text-align: right;
  color: var(--light-color);
  align-items: center;
  padding: 10px;
  font-size: 18px;
  font-weight: 600;
