}

.header {
  width: 100%;
  z-index: 500;
  position: fixed;
  top: 0;
}

.header__wrapper {
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: rgba(10, 12, 15, .9);
}

.header__wrapper.open-menu {
  background-color: var(--background-primary);
}

.logo-icon {
  width: 56px;
  height: 64px;
}

.header__navigation-bar {
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

@media screen and (min-width: 910px) {
  .header__navigation-bar {
    flex-wrap: nowrap;
  }
}

.header__menu {
  width: 100%;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 20px;
  transition: top .3s ease-in-out;
  display: none;
  position: absolute;
  top: -100%;
  left: 0;
