    -webkit-backdrop-filter: none;
    backdrop-filter: none;
    background-color: rgba(0, 0, 0, 0);
    border-bottom: none;
    margin-left: 28px;
    padding-bottom: 0;
    display: flex;
    position: static;
  }
}

.header__nav-list {
  width: 100%;
  border-bottom: 1px solid var(--background-12);
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding-top: 20px;
