    border-bottom: none;
    padding-top: 0;
  }
}

.header__nav-item {
  z-index: 100;
  letter-spacing: -.256px;
  color: var(--text-56);
  align-items: center;
  font-weight: 600;
  line-height: 1.5;
  transition: all .3s;
  display: flex;
}

.header__nav-item.active {
  color: var(--text-light);
}

.header__nav-link {
  padding: 10px 0;
}

.nav-select-icon {
  width: 24px;
  height: 24px;
  fill: var(--text-36);
}

.header__user-actions {
  letter-spacing: -.256px;
  color: var(--text-56);
  align-items: center;
  margin-top: 20px;
  font-weight: 600;
  line-height: 1.5;
  display: flex;
}

@media screen and (min-width: 768px) {
