.hero {
  background-color: var(--background-primary);
  border-bottom-left-radius: max(1.25rem, min(.75rem + 2.5vw, 3rem));
  border-bottom-right-radius: max(1.25rem, min(.75rem + 2.5vw, 3rem));
  padding-bottom: max(1.875rem, min(.268rem + 8.04vw, 7.5rem));
}

.hero__title, .hero__title--strong {
  width: max(18.75rem, min(3.036rem + 78.57vw, 73.75rem));
  text-shadow: 0 32px 64px rgba(255, 255, 255, .08);
  letter-spacing: -4px;
  color: var(--text-light);
  font-size: max(3.5rem, min(2.179rem + 6.61vw, 8.125rem));
  font-weight: 700;
  line-height: max(3.5rem, min(2.357rem + 5.71vw, 7.5rem));
