  height: fit-content;
  border: 1px solid var(--background-04);
  background: var(--background-08);
  -webkit-backdrop-filter: blur(60px);
  backdrop-filter: blur(60px);
  border-radius: 24px;
  flex-direction: column;
  align-items: center;
  padding: max(1.25rem, min(8.93vw - .536rem, 3.75rem)) max(1.25rem, min(13.39vw - 1.429rem, 5rem));
