input {
  width: 0;
  height: 0;
  visibility: hidden;
}

input:checked + .item-graph__label {
  background: linear-gradient(268deg, #577cff 0%, #4cbfff 100%), #43a1f7;
}

input:checked + .item-graph__label .item-graph__label-icon {
  top: 50%;
  left: 100%;
  transform: translate(-115%, -50%);
}

input:checked + .item-graph__label:after {
  left: 100%;
  transform: translateX(-100%);
}

.item-graph__icon-midas {
